.additional-cards {
  padding: 25px 20px 35px 20px;
  box-sizing: border-box;
}

.additional-cards__title {
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
  text-align: left;
  margin-bottom: 36px;
}

.additional-cards__list {
  list-style: none;
  padding: 0;
  margin: 0 auto;
}

.additional-card {
  width: 280px;
  padding: 13px 0 19px 0;
}

.additional-card__title {
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  text-align: left;
  padding: 0;
  margin: 0;
}

.additional-card__description {
  font-family: "Lato", sans-serif;
  font-size: 14px;
  line-height: 14px;
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 13px 0 15px 0;
}

.additional-card__button {
  width: 100%;
}
